      locales:'Grupos Locales:',
      reuniones:'Reuniones',
      text_sections_1: 'una forma de organizarnos es a través de Grupos Scouts, que pertenecen a un barrio o localidad. Los grupos se componen de varias secciones según edad.',
      text_sections_2:'Durante el año escolar los grupos se reúnen los fines de semana. Se organizan acampadas de fin de semana y en verano campamentos de dos semanas.',
