      text_sections_Esculta_4:' No aceptan el ocio pasivo y dirigido que se oferta a los jóvenes. Elaboran sus propios proyectos, abiertos al servicio comunitario, se abren a la sociedad y afianzan su pertenencia y su responsabilidad ciudadana.',
      text_sections_Esculta_5:'Conciben sus propios “proyectos de cambio”. Son ellos y ellas los verdaderos protagonistas de su futuro. Conciben la “Empresa o Proyecto” desde la idea hasta la ejecución, con la mera supervisión del educador adulto. Se responsabilizan de sus acciones y se impulsan en su propia motivación.',
      text_sections_Esculta_6:'Empiezan a plantearse su formación profesional y a fijarse retos. Se hacen conscientes de que los estudios son lo más importante de sus vidas en este momento, sin dejar de lado también sus aspiraciones sociales y sus ganas de divertirse de forma saludable. El cuidado de la salud cobra una gran importancia en esta etapa para convertirse en un adulto sano.',
      
      text_sections_Clan_1:'La última etapa de la educación scout es el Clan Rover formado por jóvenes a partir de 17 años.',
